
import { deserialize } from "utils/game/Serializer";
import { START_GAME } from "store/mutations";
export default {
	data () {
		return {
			serialization: "",
			contents: `00000000000000000
03020201010202030
00000000000000000
01110111011101110
01010101010101010
01010111010101110
00000000000000000
01110101010100100
01000111010100100
01110101010110110
00000000000000000
00000202022000000
00000020002200000
00000000000000000
00303020102030300
00000000000000000
00000000000000000`.split(/\s+/g).map(row => row.split("").map(i => parseInt(i)))
		};
	},
	created () {
		const options = deserialize(this.serialization);
		this.$store.commit(START_GAME, {
			size: options.width,
			rules: {
				row: options.row,
				column: options.column
			},
			board: this.contents,
			colors: options.colors
		});
	}
};



