<template lang="pug">
	div#app.whole(:style="colorStyling")
		section#main.main.page-section
			game-board
				floating-options
		sidebar#sidebar.sidebar
			footer
				| Created by&nbsp;
				a.author(href="https://github.com/JZumun")
					img(src="https://avatars3.githubusercontent.com/u/10479613")
					| JZumun
				| &nbsp;using #[a(href="https://vuejs.org/") Vue JS].
				| #[br] Background images from #[a(href="https://unsplash.com/") Unsplash].
				| Hosted on #[a(href="https://github.com/JZumun/nonochill-v2") Github].
		github-corner-link#github-link(href="https://github.com/JZumun/nonochill-v2")
</template>

<script>
	import { mapState } from "vuex";
	import store from "store/Store";

	import Sidebar from "components/app/Sidebar.vue";
	import GameBoard from "components/game/GameBoard.vue";
	import GithubCornerLink from "components/app/GithubCornerLink.vue";
	import FloatingOptions from "components/app/FloatingOptions.vue";

	import TitleScreen from "components/mixins/titleScreen";

	export default {
		store,
		name: "app",
		computed: mapState({
			reverse: "colorReverse",
			anchor: "colorAnchor",
			maxColor: "colorNum",
			board: "board",
			state: "mode",
			colorStyling: ({ colorScheme }) => `${colorScheme.map((color, index) => {
				return `--state-${index + 1}:${color};`;
			}).join("")}`
		}),
		components: {
			Sidebar,
			GameBoard,
			GithubCornerLink,
			FloatingOptions
		},
		mixins: [TitleScreen]
	};
</script>

<style>
.whole {
	display:grid;
  	grid-template-columns:var(--sidebar-width) 1fr;
	grid-template-areas: "sidebar game";
  	height:100vh;
  	text-transform: lowercase;
}

.sidebar {
	grid-area: sidebar;
}
.main {
	--margin: 2em;
	--game-width-restraint: calc(100vw - (var(--sidebar-width) + 2*var(--margin)));
	--game-height-restraint: calc(100vh - 2*var(--margin));

	grid-area: game;

	background-color: var(--translucent-white);
	padding:2em;

	position: relative;
	height: var(--game-width-restraint);
	width: var(--game-width-restraint);
	max-width: var(--game-height-restraint);
	max-height: var(--game-height-restraint);

	justify-self: center;
	align-self: center;
	display:flex;
}

.hidden {
	display:none !important;
}

footer {
	font-size:0.5em;
	opacity: 0.5;
	padding:2em;
	margin-top:auto;
	text-align:center;
}
footer img {
	width:2em;
	vertical-align: middle;
	border-radius:50%;
	margin-right:0.5ch;
}

.author {
	white-space: nowrap;
}

@media all and (max-width:800px) {
	.whole {
		width:100%;
		grid-template-columns: 100%;
		grid-template-rows: 1fr 1fr;
		grid-template-areas: "game" "sidebar";
	}
	.page-section {
		height: 100%;
		width:100%;
	}
	.main {
		width: calc(100vw - 2em);
		height: calc(100vw - 2em);
		margin:1em auto;
		padding:0.5em;
		max-width: 100%;
		max-height: 100%;
	}

	.sidebar {
		max-height:none;
		height:100%;
	}
}
</style>



